<template>  
    <div class="organizational-manage">
        <separator></separator>
        <div class="content">
            <div class="tree">
                <el-input
                placeholder="输入关键字进行过滤"
                v-model="filterText">
                </el-input>
                <el-tree
                ref="tree"
                :data="treeData"
                node-key="id"
                :filter-node-method="filterNode"
                :default-expanded-keys="[2, 3]"
                :default-checked-keys="[5]"
                :props="defaultProps"
                default-expand-all
                @node-drag-start="handleDragStart"
                @node-drag-enter="handleDragEnter"
                @node-drag-leave="handleDragLeave"
                @node-drag-over="handleDragOver"
                @node-drag-end="handleDragEnd"
                @node-drop="handleDrop"
                draggable
                :allow-drop="allowDrop" 
                :allow-drag="allowDrag"
                @node-click="nodeClick">
                <span class="custom-tree-node" slot-scope="{ node, data }">
                    <span>{{data.name}}</span>
                    <span>{{data.colspan-1?'('+(data.colspan-1)+')':''}}</span>
                </span>
                </el-tree>
            </div>
            <div class="table">
                <div class="util">
                    <el-button type="primary" @click="createDialogVisible = true" :disabled="!$route.meta['add']">创建新组织</el-button>
                    <el-button type="danger" @click="remove(multipleSelection)" :disabled="!$route.meta['remove']">批量删除</el-button>
                </div>
                <div class="table-box" ref="tableBox">
                    <template>
                        <el-table
                            ref="table"
                            :data="tableData"
                            @selection-change="handleSelectionChange"
                            style="width: 100%"
                            :default-sort = "{prop: 'id', order: 'ascending'}"
                            >
                            <el-table-column
                            fixed
                            type="selection"
                            width="55"
                            ></el-table-column>
                            <template v-for="(tab,index) in tableDataLabel">
                                <el-table-column
                                :key="index"
                                type="index"
                                width="50"
                                :label="tab.name"
                                v-if="tab.type == 'index'">
                                </el-table-column>
                                <el-table-column
                                    v-else
                                    sortable
                                    :key="index"
                                    :prop="tab.property"
                                    :label="tab.name"
                                    :min-width="tab.width">
                                </el-table-column>                                
                            </template>
                            <el-table-column
                            fixed="right"
                            label="操作"
                            width="150">
                            <template slot-scope="scope">
                                <el-button
                                @click.native.prevent="edit(scope.row)"
                                type="text"
                                size="small"
                                :disabled="!$route.meta['modify']">
                                编辑
                                </el-button>
                                <el-button
                                @click.native.prevent="remove(scope.row)"
                                type="text"
                                class="del-btn"
                                size="small"
                                :disabled="!$route.meta['remove']">
                                移除
                                </el-button>
                            </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
            </div>
        </div>
        <el-dialog
        title="新增"
        :visible.sync="createDialogVisible"
        width="30%"
        :before-close="createDialogFun">
            <el-form :model="createForm" status-icon :rules="createFormRules" ref="createForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="上级组织" prop="parentName">
                <el-input  v-model="createForm.parentName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="组织名称" prop="name">
                <el-input  v-model="createForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="组织描述" prop="remark">
                <el-input v-model.number="createForm.remark"></el-input>
            </el-form-item>
            </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="()=>{createDialogVisible = false;nulltoForm(createForm,'createForm')}">取 消</el-button>
            <el-button type="primary" @click="submitForm('createForm')">确 定</el-button>
        </span>
        </el-dialog>
        <el-dialog
        title="修改"
        :visible.sync="editDialogVisible"
        width="30%"
        :before-close="editDialogFun">
            <el-form :model="editForm" status-icon :rules="editFormRules" ref="editForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="上级组织" prop="parentName" v-if="editForm.code!='1001'">
                <el-input  v-model="editForm.parentName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="组织名称" prop="name">
                <el-input  v-model="editForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="组织描述" prop="remark">
                <el-input v-model.number="editForm.remark"></el-input>
            </el-form-item>
            </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="()=>{editDialogVisible = false;nulltoForm(editForm,'editForm')}">取 消</el-button>
            <el-button type="primary" @click="submitForm('editForm')">确 定</el-button>
        </span>
        </el-dialog>        
    </div>
</template>
<script>
import separator from "../../../../public/separator.vue";
import TABLE_TYEPS from "../../../../../config/tableType.js";
import APICONFIG from "../../../../../config/api.js";
export default {
  name: "organizational_manage",
  components: {
    separator
  },
  data () {
    var self = this;
    var parentNameValidate = (rule, value, callback) => {
      let is = false;
      for (let i in self.listDate) {
        self.listDate[i].name == value ? (is = true) : "";
      }
      if (is) {
        callback();
      } else {
        callback(new Error("不存在【" + value + "】这个父级!"));
      }
    };
    return {
      currentNode: "gen",
      treeData: [],
      defaultProps: {
        children: "children",
        label: "name"
      },
      listDate: [1, 2, 3, 4],
      filterText: "",
      multipleSelection: [],
      tableDataLabel: "",
      netAPI: "",
      tableData: [],
      editDialogVisible: false, //修改弹框
      createDialogVisible: false, //添加弹框
      createForm: {
        parentName: "",
        name: "",
        remark: ""
      },
      createFormRules: {
        parentName: [
          { required: true, message: "请输入组织名称", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: "长度在 2 到 20 个字符",
            trigger: "blur"
          },
          { validator: parentNameValidate, trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入组织名称", trigger: "blur" },
          { min: 2, max: 20, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ]
      },
      editForm: {
        id: "",
        code: "",
        parentName: "",
        name: "",
        remark: "",
        orderNum: "",
        gmtCreate: ""
      },
      editFormRules: {
        parentName: [
          { required: true, message: "请输入组织名称", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: "长度在 2 到 20 个字符",
            trigger: "blur"
          },
          { validator: parentNameValidate, trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入组织名称", trigger: "blur" },
          { min: 2, max: 20, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  created () {
    this.tableDataLabel = TABLE_TYEPS.get("organizational_manage");
    this.netAPI = APICONFIG().get("organizational_manage");
    this.getData();
  },
  methods: {
    nulltoForm (obj, formName) {
      for (let i in obj) {
        obj[i] = "";
      }
      this.$refs[formName].resetFields();
    },
    filterNode (value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    handleDragStart (node, ev) {
      console.log("drag start", node);
    },
    handleDragEnter (draggingNode, dropNode, ev) {
      console.log("tree drag enter: ", dropNode.name);
    },
    handleDragLeave (draggingNode, dropNode, ev) {
      console.log("tree drag leave: ", dropNode.name);
    },
    handleDragOver (draggingNode, dropNode, ev) {
      console.log("tree drag over: ", dropNode.name);
    },
    handleDragEnd (draggingNode, dropNode, dropType, ev) {
      console.log("tree drag end: ", dropNode && dropNode.name, dropType);
    },
    handleDrop (draggingNode, dropNode, dropType, ev) {
      console.log("tree drop: ", dropNode.name, dropType);
    },
    allowDrop (draggingNode, dropNode, type) {
      return true;
    },
    allowDrag (draggingNode) {
      return true;
    },
    handleSelectionChange (val) {
      if (val.length) {
        this.multipleSelection = val;
      } else {
        this.multipleSelection = null;
      }
    },
    createDialogFun () {
      this.nulltoForm(this.createForm, "createForm");
      this.createDialogVisible = false;
    },
    editDialogFun () {
      this.nulltoForm(this.editForm, "editForm");
      this.editDialogVisible = false;
    },
    edit (row) {
      //编辑框
      this.editDialogVisible = true;
      for (let index in this.editForm) {
        this.editForm[index] = row[index];
      }
    },
    getData () {
      this.$http.get(this.netAPI.find).then(res => {
        if (res.body.status == 200) {
          let data = res.body.data;
          this.listDate = data;
          //获取父级节点名字
          this.getParentName(data);

          data[0].children = this.toTree(data, data[0].code);

          this.treeData = [data[0]];
          this.tableData = [data[0]];

          //计算子节点个数
          this.count(this.treeData[0]);
        }
      });
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          switch (formName) {
            case "createForm":
              this.createFormFun();
              break;
            case "editForm":
              this.editFormFun();
              break;
            default:
              break;
          }
        } else {
          this.$message.error("请填写必填信息");
          return false;
        }
      });
    },
    editFormFun () {
      for (let i in this.listDate) {
        if (this.listDate[i].name == this.editForm.parentName) {
          this.editForm.parentCode = this.listDate[i].code;
        }
      }

      let fr = {
        code: this.editForm.code,
        name: this.editForm.name,
        parentCode: this.editForm.parentCode,
        remark: this.editForm.remark,
        orderNum: this.editForm.orderNum,
        gmtCreate: this.editForm.gmtCreate
      };

      this.$http.put(this.netAPI.put + this.editForm.id, fr).then(res => {
        if (res.body.status == 200) {
          this.getData();
          this.$message({
            message: "修改成功",
            type: "success"
          });
          this.editDialogFun();
        }
      });
    },
    createFormFun () {
      for (let i in this.listDate) {
        this.listDate[i].name == this.createForm.parentName
          ? (this.createForm.parentCode = this.listDate[i].code)
          : "";
      }
      let fr = {
        name: this.createForm.name,
        parentCode: this.createForm.parentCode,
        remark: this.createForm.remark
      };

      this.$http.post(this.netAPI.add, fr).then(res => {
        if (res.body.status == 200) {
          this.getData();
          this.$message({
            message: "创建成功",
            type: "success"
          });
          this.createDialogFun();
        }
      });
    },
    getParentName (obj) {
      for (let item of obj) {
        this.$http
          .get(this.netAPI.findParentName + item.parentCode)
          .then(res => {
            if (res.body.status == 200) {
              try {
                item.parentName = res.body.data.name;
              } catch (error) { }
            }
          });
      }
    },
    /*
    点击节点
    */
    nodeClick (data, node) {
      //当前选中的
      this.currentNode = data.name
      this.tableData = [];
      try {
        if (node.data.children.length > 0) {
          this.toArray(node.data.children);
          this.tableData.push(node.data);
        }
      } catch (error) {
        this.tableData = [node.data];
      }
    },
    toTree (data, parentCode) {
      //生成树
      let tree = [];
      let temp;
      for (let i = 0; i < data.length; i++) {
        if (data[i].parentCode == parentCode) {
          let obj = data[i];
          temp = this.toTree(data, data[i].code);
          if (temp.length > 0) {
            obj.children = temp;
          }
          tree.push(obj);
        }
      }
      return tree;
    },
    toArray (tree) {
      //树成队列
      for (let item of tree) {
        if (item.children) {
          this.tableData.push(item);
          this.toArray(item.children);
        } else {
          this.tableData.push(item);
        }
      }
    },
    count (json) {
      if (!json.children) {
        json.colspan = 1;
        return 1;
      } else {
        let leafCount = 0;
        for (let i = 0; i < json.children.length; i++) {
          leafCount = leafCount + this.count(json.children[i]);
        }
        leafCount++;
        json.colspan = leafCount;
        return leafCount;
      }
    },
    remove (row) {
      let ids = [];
      if (!row) {
        this.$message({
          message: "请选中要删除的行",
          type: "warning"
        });
        return;
      }
      //判断类型
      let rowType = Object.prototype.toString.call(row);
      if (rowType == "[object Array]") {
        for (let index in row) {
          ids.push(row[index].id);
        }
      } else {
        ids.push(row.id);
      }
      //弹出对话框
      this.$confirm("确定要删除选中的行?", "删除提示", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "不删除"
      }).then(() => {
        //确认删除
        for (let item of ids) {
          this.$http
            .delete(this.netAPI.delete + item)
            .then(res => {
              if (res.data.status == 200) {
                this.getData();
                this.tableData = [];
              }
            });
        }
      });
    }
  },
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val);
    },
    currentNode (val) {
      this.createForm.parentName = val;
      this.editForm.parentName = val;
    }
  }
};
</script>

<style lang="scss" scoped>
.util {
  text-align: right;
}
.organizational-manage {
  width: 100%;
  height: 100%;
  overflow: auto;
  .content {
    width: 100%;
    & > div {
      display: inline-block;
    }
    .tree {
      width: 23%;
      padding: 10px;
      vertical-align: top;
    }
    .table {
      width: 73%;
    }
  }
}
</style>



